body {
  background: yellow;
  margin: 0 auto;
  max-width: 1600px;
}
body #image-wrapper {
  display: grid;
  width: 1500px;
  margin: 0 auto;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: repeat(5, 20%);
  height: 80vh;
}
body #image-wrapper .item {
  height: 220px;
  width: 255px;
  cursor: pointer;
}
body #image-wrapper #rock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-column-start: 2;
  grid-row-start: 4;
}
body #image-wrapper #paper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-row-start: 4;
  grid-column-start: 3;
}
body #image-wrapper #scissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-row-start: 4;
  grid-column-start: 4;
}
body #image-wrapper .computerrock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}
body #image-wrapper .computerpaper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}
body #image-wrapper .computerscissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}